const state = {
  activity_id:'', //activities.id
  userActivities_id:'',  //userActivities id
  activityInfo:null, //
  user_id:'', //
  loginUserInfo: null, //
  login: true,   //
  companyName:'', //
  userInfo:null, //id
  userInfo2:null, //id
  nurseTel:'',    ////
  companyInfo:null, ///
  projectInfo:null, ///
  projectInfo2:null, ///
  footerInfo:null  ///footer 
	}

export default new Vuex.Store({
	state,
	getters,
	actions,
	mutations,
})



